@import "../../global.scss";
.intro{
    background-color: rgb(83, 83, 83);
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    .left{
        flex:0.5;
        overflow: hidden;
        .imgContainer{
            height: 120vh;
            width: 120vh;
            background-color: $textColor;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: flex-start;
            }
            img{
                top: 100vh;
                margin-left: 5vw;
                display: flex;
                align-items: flex-end;
                justify-content: center;
                float: right;
                width:60%;
                border-radius: 50%;

                @include mobile{
                    width: 50%;
                }
            }
        }
    }
    .right{
        flex:0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 100px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            color: $textColor;

            @include mobile{
                padding-left: 0;
                align-items: center;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 40px;

                @include mobile{
                    font-size: 20px;
                }
            }
            h3{
                font-size: 30px;
                @include mobile{
                    font-size: 15px;
                }

                span{
                    font-size: inherit;
                    color: khaki;
                }

                .ityped-cursor{
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 30%;
            .icon{
                width: 100px;
                height: 100px;
                color: burlywood;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                from{
                    opacity: 0.8;
                }
                to{
                    opacity: 0;
                }
            }
        }
        
    }
}